*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
    user-select: none;
    font-family: "poppins","lato",system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
body{
    background-color: #f1f1f1;
}
.app-body{
    width: 100%;
}
.app-body .section-wrapper{
    width: 100%;
    padding: 30px 0 60px;
    position: relative;
}
.section-wrapper .header-wrapper{
    display: grid;
    position: fixed;
    top: 0;
    width: 100%;
    background-color: black;
    z-index: 1;
    grid-template-columns: auto auto auto;    
}
.header-wrapper .header-inner-back-btn{
    display: flex;
    flex-flow: wrap row;
    justify-content: flex-start;
    align-items: center;
}
.header-inner-back-btn button{
    margin: 8px;
    padding: 3px 8px;
    font-size: 12px;
    border: 0;
    outline: 0;
    border-radius: 6px;
}
.header-inner-back-btn button:hover{
    transition: 0.3s;
    background-color: #ffffffc3;
}
.header-wrapper .header-inner-title {
    display: flex;
    flex-flow: wrap row;
    justify-content: center;
    align-items: center;
    color: white;
}
.header-inner-title h3{
    font-size: 16px;
}
.header-wrapper .header-inner-admin-btn{
    display: flex;
    flex-flow: wrap row;
    justify-content: flex-end;
    align-items: center;
}
.header-inner-admin-btn button{
    margin-right:10px;
    padding: 3px 8px;
    font-size: 12px;
    border: 0;
    outline: 0;
    border-radius: 6px;
}
.header-inner-admin-btn button:hover{
    transition: 0.3s;
    background-color: #ffffffc3;

}
.main-wrapper{
    width: 100%;
    display: grid;
    grid-template-columns: 85%;
    justify-content: center;
}
.main-wrapper .main-inner-wrapper{
    margin-top: 20px;
    padding: 15px;
    position: relative;
    display: flex;
    flex-flow: wrap row;
    justify-content: center;
    box-shadow:-5px -5px 14px #fff,5px 5px 8px #aeaec0;
    align-items:center;
}
.main-inner-wrapper h4{
    font-size: 18px;
    position: absolute;
    color: white;
    padding: 0 30px;
    text-align: center;
    font-weight: 500;
}
.main-inner-wrapper img{
    width: 100%;
    filter: brightness(45%);
}
.main-inner-wrapper img:hover{
    transition: 0.3s;
    filter: brightness(80%);
}
.main-inner-wrapper .Xmark{
    position: absolute;
    bottom: 1px;
    right: 1px;
    font-size: 15px;
    color: black;
}
.main-inner-wrapper .Xmark:hover{
    transition: 0.3s;
    color: red;
}
.album-img-wrapper{
    width: 95%;
    display: grid;
    grid-template-columns: 85%;
    justify-content: space-evenly;
    box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 10px #aeaec0;
    margin: auto;
    padding: 20px 0 0;
}
.album-img-wrapper .album-img-inner-wrapper{
    display: flex;
    margin-bottom: 20px;
    box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
    align-items: center;
    position: relative;
}
.album-img-inner-wrapper img{
    padding: 14px;
    display: block;
    filter: brightness(60%);
    margin: auto;
    overflow: hidden;
}
.album-img-inner-wrapper .Xmark{
    position: absolute;
    bottom: 1px;
    right: 1px;
}
.album-img-inner-wrapper .Xmark:hover{
    transition: 0.3s;
    color: red;
}
.section-wrapper form{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 90%;
    padding: 25px 0 15px;
    box-shadow:-5px -5px 14px #fff,5px 5px 8px #aeaec0;
}
.section-wrapper form input{
    display: block;
    margin: 0 auto 15px;
    padding:8px 14px;
    width: 85%;
    font-size: 15px;
    box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 10px #aeaec0;
    border: 0;
    outline: 0;
    border-radius: 14px;
}
.section-wrapper form .show{
    margin: 10px 10px 0;
    text-align: center;
    color: red;
    font-size: 15px;
}
.section-wrapper form .hide{
    display: none;
}
.section-wrapper form button{
    display: block;
    margin: auto;
    text-decoration: none;
    border-radius: 7px;
    font-size:15px;
    color: black;
    padding: 2px 10px;
    box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
    border: 0;
    outline: 0;
}
.section-wrapper form button:hover{
    color: darkblue;
    transition: 0.3s;   
    box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
}
.app-body .footer-wrapper{
    width: 100%;
    position: fixed;
    background-color: #f1f1f1;
    bottom: 0px;
    left: 0;
}
.upload-wrapper .upload-inner-wrapper{
    width: 90%;
    margin:20px auto;
    padding: 17px 0px;
    box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 10px #aeaec0;
    border-radius: 20px;
}
.upload-inner-wrapper input{
    display: block;
    box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 10px #aeaec0;
    margin: 0px auto;
    border-radius: 14px;
    font-size: 15px;
    width: 88%;
    padding: 8px 12px;
    border: 0;
    outline: 0;
}
.upload-inner-wrapper input[type="file"]::-webkit-file-upload-button{
    border: 0;
    outline: 0;
    margin: 8px 8px 8px 5px;
    color: black;
    font-weight: bold;
    font-size: 12px;
    padding: 5px 8px;
    border-radius: 8px;
    box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
}

.upload-inner-wrapper input[type="file"]::-webkit-file-upload-button:hover{
    box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
    color: red;
}
.upload-inner-wrapper button{
    text-decoration: none;
    border-radius: 7px;
    font-size:18px;
    color: black;
    padding: 2px 8px;
    box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
    border: 0;
    outline: 0;
    display: block;
    margin: auto;
}
.upload-inner-wrapper .upload-inner-box{
    display: flex;
    flex-flow: wrap row;
    justify-content: space-between;
    align-items: center;
    width: 88%;
    margin: 12px auto 0;
}
.upload-inner-box #bg_btn{
    margin: 0;
}
.upload-inner-box #bg_url{
    width: 80%;
    margin: 0;
    border-radius: 18px;
    padding: 5px 8px;
    font-size: 10px;
}
.upload-inner-wrapper button:hover{
    color: red;   
    box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
    transition: 0.3s;
}
.footer-wrapper ul{
    list-style-type: none;
    padding: 8px 0;
    display: grid;
    grid-template-columns: auto auto auto;
}
.footer-wrapper ul li{
    padding:4px 8px;
    text-align: center;
}
.footer-wrapper ul li .a{
    text-decoration: none;
    border-radius: 7px;
    font-size: 15px;
    color: black;
    padding: 2px 8px;
    box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
}
.footer-wrapper ul li .a:hover{
    transition: 0.3s;
    color: darkblue;
    box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
}

.section-wrapper .album-title-wrapper{
    background-color: darkblue;
    width: 96%;
    border-radius: 5px;
    margin: 8px auto;
}
.album-title-wrapper h4{
    text-align: center;
    color: white;
    font-size: 12px;
    padding: 5px;
    font-weight: 500;
    letter-spacing: 0.5px;
}
.section-wrapper .contact-wrapper{
    width: 100%;
}
.contact-wrapper .contact-inner-header{
    width: 94%;
    display: flex;
    flex-flow: wrap row;
    margin: 10px auto 20px;
    box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 10px #aeaec0;
    padding: 15px;
}
.contact-inner-header img{
    width: 100%;
    padding: 8px;
    margin-bottom: 15px;
    box-shadow:-5px -5px 14px #fff,5px 5px 8px #aeaec0;
}
.contact-inner-header img:hover{
    box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 10px #aeaec0;
    transition: 0.3s;
}
.contact-wrapper .contact-inner-social{
    width: 100%;
    margin: auto;
    display: flex;
    flex-flow: wrap row;
    justify-content: space-around;
}
.contact-wrapper .embed-inner-wrapper{
    margin: 15px auto;
    width: 95%;
    padding: 20px;
    box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 10px #aeaec0;
}
.embed-inner-wrapper iframe{
    filter: brightness(92%);
    border-radius:20px;
    border: 0;
    padding: 10px;
    box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
    width: 100%;
    height: 120px;
    margin: auto;
    display: block;
}
.embed-inner-wrapper h5{
    text-align: center;
    box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
    margin-bottom: 15px;
    border-radius: 15px;
    color: black;
    font-size: 13px;
    padding: 10px 0;
}
.contact-inner-social .i{
    box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
    font-size: 16px;
    padding: 5px 8px;
    color: black;
    border-radius: 6px;
}
.contact-inner-social .i:hover{
    color: darkblue;
    transition: 0.3s;
    box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
}
.contact-inner-header .contact-call{
    display: flex;
    flex-flow: wrap row;
    justify-content: space-between;
    width: 100%;
}
.contact-call a{
    display: flex;
}
.contact-call .i{
    align-self: center;
    box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
    font-size: 16px;
    padding: 5px 8px;
    color: black;
    border-radius: 6px;
}
.contact-call .i:hover{
    transition: 0.3s;
    box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
    color: darkblue;
}
.contact-inner-header h4{ 
    box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
    padding: 5px 12px;
    color: black;
    border-radius: 5px;
}
.contact-inner-header h4:hover{
    box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
    transition: 0.3s;
    color: red;
}
.contact-footer-wrapper{
    display: flex;  
    border-radius: 20px;
    box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
    width: 95%;
    margin: auto;
    padding-bottom: 15px;
    flex-flow: wrap row;
    justify-content: space-evenly;
}
.contact-footer-wrapper h6{
    width: 100%;
    text-align: center;
    font-size: 14px;
    color: red;
    padding: 10px 0;
}
.contact-footer-wrapper div{
    padding: 5px 0;
    width: 45%;
    margin: 0;
    box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
    color: black;
    border-radius: 12px;
    font-weight: bold;
}
.contact-footer-wrapper .contact-footer-left{
    width: 35%;
}
.contact-footer-wrapper .contact-footer-right{
    width: 50%;
}
.contact-footer-wrapper div p{
    font-size: 12px;
    text-align: center;
    margin: 2px 0;
}
@media screen and (max-width:280px){
    .embed-inner-wrapper h5{
        font-size: 11px;
    }    
    .contact-call h4{
        font-size: 14px;
    }
    .contact-call .i{
        font-size: 12px;
    }
    .contact-footer-wrapper div p{
        font-size: 11px;
    }
}
@media screen and (max-width:260px){
    .embed-inner-wrapper h5{
        font-size: 4vw;
    }    
    .contact-call h4{
        font-size: 13px;
    }
    .contact-footer-wrapper div p{
        font-size: 4.5vw;
    }
}
.service-wrapper{
    width: 100%;
}
.service-wrapper .service-inner-wrapper{
    width: 95%;
    margin: 15px auto;
    padding: 10px 0 15px;
    box-shadow:inset -5px -5px 12px #fff,inset 5px 5px 10px #aeaec0;
}
.service-inner-wrapper h2{
    text-align: center;
    box-shadow:-3px -3px 8px #fff,3px 3px 6px #aeaec0;
    width: 88%;
    padding: 5px 0;
    margin: 5px auto;
    border-radius: 18px;
    font-size: 15px;
    color:black;
}
.service-inner-wrapper h2:hover{
    box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
    color: red;
    transition: 0.3s;
}
.service-inner-wrapper p{
    margin: 10px auto 0;
    font-size: 12px;
    text-align: justify;
    font-weight: 500;
    word-break: break-all;
    padding: 8px 14px;
    box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;
    width: 88%;
}
.service-img-wrapper{
    width: 90%;
    display: grid;
    grid-template-columns: 48% 48%;
    justify-content: space-between;
    margin: 5px auto;
    padding: 10px 0 0;
}
.service-img-inner-wrapper{
    box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;    
    display: flex;
    margin-bottom: 10px;
    border-radius: 10px;
}
.service-img-inner-wrapper img{
    padding: 10px;
    border-radius: 15px;
    filter: brightness(90%);
    width: 100%;
}
.service-img-inner-wrapper img:hover{
    transition: 0.3s;
    filter: brightness(100%);
    transform: scale(1.1,1.1);
}
.service-inner-wrapper video{
    width: 88%;
    display: block;
    margin: 12px auto 0;
    box-shadow:inset -3px -3px 6px #fff,inset 3px 3px 6px #aeaec0;    
    padding: 10px;
    filter: brightness(80%);
    border-radius: 20px;
}
.service-inner-wrapper video:hover{
    filter: brightness(100%);
    transform: scale(1,1);
    transition: 0.3s;
}
.comment-inner-wrapper{
    background-color:darkblue;
    border-radius: 20px;
    margin: 0px 10px;
    position: relative;
    left: 0%;
    animation-name: leftSlide;
    animation-duration: 7s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}
@keyframes leftSlide{
    0%{
        left: 0%;
    }    
    15%{
        left: -100%;
    }
    30%{
        left: -100%;
    }
    45%{
        left: -200%;
    }
    60%{
        left: -200%;
    }
    75%{
        left:-300%;
    }
    100%{
        left:-300%;
    }
}
.comment-wrapper{
    width: 100%;
    display: grid;
    grid-template-columns: 100% 100% 100% 100%;
    overflow: hidden;
}
.comment-inner-wrapper p{
    color: white;
    font-size: 12px;
    text-align: justify;
    padding: 0 15px 15px;
}
.comment-inner-wrapper h5{
    padding: 10px 0 5px;
    color: white;
    font-size: 15px;
    text-align: center;
}
.head-wrapper{
    width: 100%;
    margin-top: 10px;
}
.head-wrapper .head-inner-wrapper{
    width: 85%;
    margin: 25px auto 0;    
    box-shadow:-5px -5px 16px #fff,inset 5px 5px 10px #aeaec0;border-radius: 20px;
    padding: 20px 20px 10px;
}
.head-inner-wrapper img{
    width: 100%;
    border-radius: 20px;
    box-shadow:-5px -5px 14px #fff, 5px 5px 10px #aeaec0;
}