*{
    box-sizing: border-box;
}
.loader-wrapper{
    width: 100%;
    padding: 20px;
}
.loader-wrapper .loader-container{
    height: 91vh;
    box-shadow:inset -5px -5px 30px #fff,inset 5px 5px 10px #aeaec0;
    display: flex;
    flex-flow: wrap row;
    justify-content: center;
    border-radius: 20px;
    align-content: center;
    width: 100%;
}
.loader-container .loader{
    width: 80%;
    box-shadow:inset -5px -5px 40px #fff,inset 5px 5px 10px #aeaec0;
    border-radius: 100%;
    padding:30px 0;
}
.loader img{
    width: 100%;
    animation-name: load;
    animation-duration: 600ms;
    animation-timing-function: linear;
    transform: scale(1,1);
}
@keyframes load{
    from{
        transform: scale(0.2,0.2);
    }
    to{
        transform: scale(1,1);
    }
}