*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.fullview-wrapper{
    background-color: black;
    border: 1px solid black;
    width: 100%;
    height: 87vh;
    overflow: hidden;
    display: flex;
    flex-flow: wrap row;
    justify-content: center;
    align-content: center;
    position: relative;
}
.fullview-wrapper button{
    position: absolute;
    top: 6px;
    font-size: 14px;
    padding: 3px 8px;
    left: 6px;
    border-radius: 8px;
    border: 0;
    outline: 0;
}
.fullview-wrapper button:hover{
    transition: 0.3s;
    background-color: rgba(255, 255, 255, 0.754);
}
.fullview-wrapper .fullview-inner-wrapper{
    width: 100%;
    display: flex;
}
.fullview-inner-wrapper img{
    width: 100%;
    margin: auto;
    max-height: 340px;
    padding: 5px;
}